
.blog {
    display: flex;
    justify-content: center;

    &__img {
        max-width: 555px;
        width: 100%;
    }
    
    
    &__box {
        margin-top: 99px;
    }
    
    &__title {
        font-family: Muli;
        font-size: 50px;
        line-height: 40px;
        font-weight: 800;
        letter-spacing: -0.02em;
        color: #f7ae3b;
        margin-bottom: 49px;
    }
    
    &__content {
        display: flex;
        flex-direction: column;
        max-width: 539px;
        width: 100%;
        align-items: flex-end;
        position: relative;
    }
    
    &__wrapper {
        max-width: 1155px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding-bottom: 202px;
    }
    
    &__subtitle {
        font-size: 30px;
        line-height: 40px;
        font-weight: 800;
        letter-spacing: -0.02em;
        color: #f2612d;
        margin-top: 328px;
      
      
    }
    
    &__content::before {
        display: block;
        content: "";
        background: url(../images/Line-or.png);
        width: 687px;
        height: 12px;
        top: 273px;
        left: -118px;
        position: absolute;
        z-index: -1;
    }
    
    &__time {
        font-family: Muli;
        font-size: 16px;
        line-height: 35px;
        font-weight: 400;
        color: #1e1e1e;
        margin-top: 20px;
        margin-right: auto;
        margin-left: 18px;
    }
    
    &__text {
        font-size: 18px;
        line-height: 35px;
        font-weight: 400;
        color: #1e1e1e;
        margin-top: 45px;
        max-width: 472px;
        width: 100%;
    }
    
    &__link {
        text-decoration: none;
        font-weight: 800;
        font-style: italic;
       
    }
}

