

@media all and (max-width: 1335px) {
    .nav__list {
        display: none;
    }
    .header__right-block {
        display: none;
    }
   .burger {
    display: block;
    position: fixed;
    right: 20px;
    z-index: 1111;
    background-color: #f2612d;
    width: 50px;
    height: 50px;
    border-radius: 50%;
   }

   

}



@media all and (max-width: 768px) {

.smoosies__item {
    flex-basis: 50%;
}
.smoosies {
    padding: 100px 30px 110px;
}

.smoosies__item:first-child,
.smoosies__item:nth-child(2),
.smoosies__item:nth-child(3),
.smoosies__item:nth-child(4), 
.smoosies__item:nth-child(5), 
.smoosies__item:nth-child(6) 
 {
    border-bottom: 2px solid #f6edde;
}

.smoosies__item:first-child,
.smoosies__item:nth-child(3),
.smoosies__item:nth-child(5),
.smoosies__item:nth-child(7)  {
    border-right: 2px solid #f6edde;
}
.smoosies__item:nth-child(2),
.smoosies__item:nth-child(6)  {
    border-right: none;
}

}
@media all and (max-width: 482px) {
    .smoosies__desc {
        font-size: 20px;
    }
}

@media all and (max-width: 360px) {
    .smoosies__title {font-size: 36px;
}
.smoosies__text{
    font-size: 16px;
}
.smoosies {
    padding: 50px 10px 60px;
}



}





@media all and (max-width: 1500px) {
  .about__wrapper {
    height: auto;
    flex-direction: column;
    align-items: center;
    padding-bottom: 85px;
    padding-left: 10px;
    padding-right: 10px;
  }

.about__picture {
    margin-right: 0px;
}
.about__blok {
    margin-left: 0;
}

}

@media all and (max-width: 768px) {
    .about__btn {
        font-size: 20px;
    }
}


@media all and (max-width: 742px) {
    .about__picture {
        transform: translate(0px, -38px);
    }

   

} 
@media all and (max-width: 620px) {
.about__title--green::before {
    display: none;
}
}


