}

@media all and (max-width: 1050px) {
    .story__picture {
        display: block;
        padding-top: 365px;
    padding-bottom: 80px;
    transform: translateX(57px);
    }
    .story__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 20px 0;
    }
    .story__wrapper {
        margin-left: 30px;
        padding-top: 80px;
    }
    .story__img::before {
        top: 409px;
    left: -88px;
    }
}

@media all and (max-width: 747px) {
    .story__picture {
        transform: translateX(0px);
        padding-bottom: 0;
    }
}

@media all and (max-width: 672px) {
    .story__img::before {
        display: none;
    }
    
