.menu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: #0aa566;
    z-index: 1;
    transform: translateX(-100%);
    transition:transform 500ms ease;
    display: flex;
    flex-direction: column;
    display: flex;
    align-items: center;

    &.active {
        transform: translateX(0);
        z-index: 1111;
    }
    
    &__nav, &__top {
        z-index: 2;
        width: 250px;
    }
    
    &__top-block {
    display: flex;
    justify-content: space-between;
    margin-top: 120px;
    }
    
    &__social {
        margin-top: 40px;
        margin-bottom: 40px;
    }
    
    &-social__list {
        display: flex;
        width: 250px;
        justify-content: space-between;
        
    }
    
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 350px;
        justify-content: space-between;
    }
    &-social__list-item {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        width: 38px;
        height: 38px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }

}

