
.catalog {

    
&__wrapper {
    display: flex;
    justify-content: space-between;
    overflow-x: auto;
}

&__blocks {
    flex-grow: 1;
  
}

&__blocks {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 78px;
}

&__img {
    margin: 129px 0 25px;
    
}

&__title {
   
    font-size: 24px;
    line-height: 22px;
    font-weight: 700;
    
    margin-bottom: 5px;
}

&__volume {
    font-size: 21px;
    line-height: 32px;
   
    font-style: italic;
    margin-bottom: 60px;
}

&__btn {
    width: 207px;
    height: 63px;
    border: 2px solid #ffffff;
    border-radius: 30px;
    color: #ffffff;
    font-size: 21px;
    line-height: 22px;
    font-weight: 800;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px 0 30px;
}

&__btn::after {
    content: "";
    position: absolute;
    background: url(../images/Arrow-W.png);
    display: block;
    top: 23px;
    right: 40px;
    width: 30px;
    height: 16px; 
}

&__blocks--red {
    background-color: #68291c;
}

&__blocks--green {
    background-color: #178370;
}

&__blocks--yellow {
    background-color: #f3ac37;
}

&__blocks--orange {
    background-color: #c5481b;
}


&__btn:hover:after {
    transform: translateX(10px);
    -webkit-transform: translateX(10px);
    -moz-transform: translateX(10px);
    -ms-transform: translateX(10px);
    -o-transform: translateX(10px);
}


}
