
.story {

    &__content {
        display: flex;
        justify-content: center;
        padding: 0 20px 0;
    }
    
    &__title {
       
        color: #f7ae3b;
    }
    
    &__wrapper {
        max-width: 511px;
        width: 100%;
        height: 118px;
        margin-right: 206px;
        padding-top: 260px;
    }
    
    &__text {
        font-family: Muli;
        font-size: 18px;
        line-height: 35px;
        font-weight: 400;
        color: #1e1e1e;
        padding-top: 43px;
        padding-bottom: 56px;
    }
    
    &__picture {
        margin-top: 120px;
        margin-bottom: 160px;
        position: relative;
        max-width: 585px;
        width: 100%;
    }
    
    &__img {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            background: url(../images/Stamp.png);
            top: 160px;
            left: -166px;
            width: 290px;
            height: 216px;
            z-index: 1;
            
        }
    }
    
   
    
    
    &__btn {
        max-width: 304px;
        width: 100%;
        height: 63px;
        background-color: #0aa566;
        border-radius: 30px;
        font-family: Muli;
        font-size: 21px;
        line-height: 22px;
        font-weight: 800;
        color: #ffffff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 30px 0 30px;
        -webkit-border-radius: 30px;
        -moz-border-radius: 30px;
        -ms-border-radius: 30px;
        -o-border-radius: 30px;
        border: none;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            background: url(../images/Arrow.png);
            display: block;
            top: 25px;
            right: 40px;
            width: 30px;
            height: 16px;   
        }
        
        &:hover:after {
            transform: translateX(10px);
            -webkit-transform: translateX(10px);
            -moz-transform: translateX(10px);
            -ms-transform: translateX(10px);
            -o-transform: translateX(10px);
        }
    }
    
   
    

}
