
.footer {
    background-color: #ebae50;

    &__top {
        display: flex;
        justify-content: space-around;
    }
    
    &__box {
        display: flex;
        gap: 19px;
        flex-wrap: wrap;
        max-width: 270px;
        align-items: center;
    }
    
    &__list {
        list-style-type: none;
        margin-bottom: 100px;
    }
    
    &-logo {
        display: flex;
    }
    
    
    &__title {
        font-family: Muli;
        font-size: 26px;
        line-height: 40px;
        font-weight: 800;
        letter-spacing: -0.02em;
        color: #ffffff;
        margin-top: 79px;
    }
    
    
    &__title {
        margin-bottom: 43px;
    }
    
    &__item {
        font-family: Muli;
        font-size: 18px;
        line-height: 35px;
        font-weight: 700;
        letter-spacing: -0.02em;
        color: #6d450c;
    }
    
    &__input {
        max-width: 498px;
        width: 100%;
        height: 63px;
        background-color: #ffffff;
        border-radius: 30px;
        text-indent: 60px;
        border: none;
    }
    
    &__span {
        font-family: Muli;
        font-size: 18px;
        line-height: 40px;
        font-weight: 800;
        text-transform: uppercase;
        letter-spacing: -0.02em;
        color: #6d450c;
        background-color: #ebae50;
        border-color: #ebae50;
        position: relative;
    }
    
    &__input::placeholder {
        font-family: Muli;
        font-size: 17px;
        line-height: 35px;
        font-weight: 700;
        letter-spacing: -0.02em;
        color: #6d450c;
        position: relative;
    }
    
    &__form {
        position: relative;
    }
    
    &__form::before {
        content: "";
        position: absolute;
        background: url(../images/Icon1504.png);
        width: 24px;
        height: 19px;
        top: 21px;
        left: 24px;
        display: block;
    }
    
    
    &__wrapper {
        display: flex;
        margin-top: 36px;
        margin-right: 40px;
        margin-left: 116px;
        justify-content: flex-end;
    }
    
    &__span {
        border: none;
        margin-right: 40px;
        position: relative;
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            background: url(../images/Arrow1111.png);
            width: 48px;
            height: 43px;
            left: -65px;
            top: -10px;
        }
    }
    
   
    
    &__bottom {
        background-color: #6c2d19;
        display: flex;
        justify-content: center
    }
    
    
    &__small {
        font-family: Muli;
        font-size: 13px;
        line-height: 35px;
        font-weight: 400;
        letter-spacing: 0.02em;
        color: #af8a82;
        margin-top: 23px;
        margin-bottom: 19px;
    }
    &__bottom {
        position: relative;
       
    }
    
   
    
    &__btn {
        display: block;
        position: relative;
        width: 53px;
        height: 53px;
        background-color: #f2662f;
        border-radius: 5px;
        bottom: 52px;
        right: 72px;
        position: fixed;

        &::after {
            content: "";
            display: block;
            position: absolute;
            background: url(../images/Icon-up.png);
            width: 13px;
            height: 25px;
            left: 19px;
            top: 12px;
        }

        &::before {
            content: "Go top";
            position: absolute;
            font-family: Muli;
        font-size: 16px;
        line-height: 40px;
        font-weight: 700;
        letter-spacing: -0.02em;
        color: #f2662f;
        left: -57px;
            top: 44px;
        display: block;
        }
    }
    
    &__link {
        position: relative;
        display: block;
        /* position: fixed; */
    }
    
    
}



