.header {
    background-image: url(../images/header-bg-img.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;

    
&__top {
    display: flex;
    justify-content: space-between;
    padding-top: 70px;
    align-items: center;
    position: relative;
}

&__count {
    position: absolute;
    margin-top: -18px;
    margin-left: 5px;
    width: 21px;
    height: 21px;
    background-color: #ffffff;
    border-radius: 50%;
    text-align: center;
    width: 21px;
    height: 21px;
    color: black;
}

&__logo {
    display: block;
    position: absolute;
    top: 63px;
    left: 50%;
    transform: translateX(-50%);}

&__right-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

&__list {
    display: flex;
    align-items: center;
    position: relative;
}

&__list::before {
    content: '';
    position: absolute;
    top: 13px;
    left: 206px;
    background-image: url(../images/Line-h.png);
   width: 50px;
  height: 2px;
}

&__item + &__item {
    margin-left: 45px;
}

&__social {
    margin-left: 134px;
}


&__wrapper {
    display: flex;
    justify-content: space-around;
    margin-top: 100px;
}

&__btn {
    width: 280px;
    height: 70px;
    background-color: #0aa566;
    position: relative;
    border-radius: 35px;   
    &::after {
        content: "";
        position: absolute;
        background: url(../images/Arrow.png);
        display: block;
        top: 26px;
        right: 50px;
        width: 30px;
        height: 16px;  
    }
    &:hover::after {
        transform: translateX(10px);
        -webkit-transform: translateX(10px);
        -moz-transform: translateX(10px);
        -ms-transform: translateX(10px);
        -o-transform: translateX(10px);
    }
    
    }
    &__text {
        margin-left: 38px;
    }
    
    
}




