
.smoosies {
    padding: 100px 0 110px;


    
&__title {
    text-align: center;
    
    color: #f7ae3b;
    margin-bottom: 50px;

}

&__text {
    text-align: center;
    max-width: 915px;
    color: #1e1e1e;
    margin: 0 auto 60px;
   
}

&__bloks {
    max-width: 1010px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

&__item {
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 190px;
    padding-bottom: 25px;
}

&__item:first-child,
&__item:nth-child(2),
&__item:nth-child(3) {
    border-bottom: 2px solid #f6edde;
    border-right: 2px solid #f6edde;
}

&__item:nth-child(4) {
    border-bottom: 2px solid #f6edde;
}

&__item:nth-child(5),
&__item:nth-child(6),
&__item:nth-child(7) {
    border-right: 2px solid #f6edde;
}


&__desc {
    font-size: 25px;
    line-height: 40px;
    font-weight: 800;
    letter-spacing: -0.02em;
    color: #f2612d;
    margin-top: 25px;
}
}
