
.social__list {
    display: flex;
    align-items: center;
    &-item + .social__list-item {
        margin-left: 8px;
    }
    
    &-item {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        width: 38px;
        height: 38px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    

}

