
.reviews {
    background-color: #632d19;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        background: url(../images/Dots.png);
        top: -82px;
        right: 127px;
        width: 410px;
        height: 301px;
    
    }

    
&__wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
}

&__box {
    display: flex;
    align-items: center;
    max-width: 734px;
    width: 100%;
    justify-content: space-between;
    margin-top: 83px;
    position: relative;
    &::after {
        content: "Sarah A";
    
        position: absolute;
    
        display: block;
    
        opacity: 0.6;
    
        font-size: 200px;
        line-height: 40px;
        font-weight: 800;
        letter-spacing: -0.02em;
        color: #521905;
    
        top: 0;
        left: -135px;
        
    }
}



&__box {
    position: relative;
}



&__title {
    font-family: Muli;
    font-size: 50px;
    line-height: 40px;
    font-weight: 800;
    letter-spacing: -0.02em;
    color: #f7ae3b;
    margin-top: 117px;
    text-align: center;
}

&__article {
    padding-top: 83px 0 240px;
    max-width: 455px;
    width: 100%;
    height: 130px;
    text-align: center;
    z-index: 1;
}

&__title--big {
    color: #f2612d;
}

&__img {
    z-index: 1;
}

&__container {
    display: flex;
    width: 158px;
    justify-content: space-between;
    margin-bottom: 80px;
    margin-top: 110px;
}


&__btn {
    background-color: #632d19;
    border: none;
    background: url(../images/ArrowL.png);
    width: 30px;
    height: 16px;
    
}

&__title {
    margin-top: 55px;
}
&__box {
    margin-top: 75px;
}

&__quote {
    font-family: Muli;
    font-size: 18px;
    line-height: 35px;
    font-weight: 400;
    
}

&__cite {
    line-height: 45px;
    font-weight: 600;
    font-style: italic;
    color: #f7ae3b;
}
}
