
.instagram {

    
&__title {
    font-family: Muli;
    font-size: 24px;
    line-height: 40px;
    font-weight: 800;
    letter-spacing: -0.02em;
    color: #0aa566;
    position: relative;
    text-align: center;
}

&__span {
    position: relative;
    display: flex;
    justify-content: center;
    &::before {
        content: "";
        background:url(../images/icon_3294.png);
        position: relative;
        display: block;
        top: 9px;
        left: -1px;
        width: 26px;
        height: 26px;
        
    }
}



&__wrapper {
    font-size: 0;
    display: flex;
    margin-top: 45px;
    padding-bottom: 10px;
    overflow-x: auto;
}

&__photo {
    position: relative;
    &::before {
        content: "";
        display: block;
        background: url(../images/Icon1-1.png);
        width: 26px;
        height: 26px;
        bottom:20px;
        right: 20px;
        position: absolute;
       
    }
}



&__img {
    width: 384px;
height: 384px;
}

}
