
.about {
    background-color: #137f70;
    margin: 0 auto;


    &__wrapper {
        display: flex;
       height: 800px;
        position: relative;
        justify-content: center;
        padding-top: 97px;
    }
    
    &__picture {
        display: block;
        transform: translate(10px, -33px);
        max-width: 691px;
        max-height: 818px;
        width: 100%;
    }
    
    
    &__text {
        margin: 100px 0 96px 360px;
      
    }
    
    &__title {
        max-width: 577px;
        letter-spacing: -0.02em;
        width: 100%;
    }
    
    &__title--green {
        color: #cae941;
    }
    
    &__title--green::before {
        content: "";
        position: absolute;
        background: url(../images/Line.png);
        display: block;
        top: 93px;
        left: 26px;
        width: 450px;
        height: 13px;
        
    }
    
    &__blok {
        position: relative;
        max-width: 588px;
        width: 100%;
        margin-left: 140px;
    }
    
    &__item {
     position: relative;
       font-size: 21px;
    }
    
    &__item:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../images/badge.png);
        padding: 0;
        margin-right: 20px;
    
    }
    
    &__list {
        list-style-image: url(../images/1w.png);
        margin-top: 68px;
        left: 24px;
        display: flex;
        flex-direction: column;
        height: 329px;
        justify-content: space-between;
        margin-left: 22px;
    }
    
    
    
    &__btn {
        max-width: 382px;
        height: 63px;
        border: 2px solid #f8b231;
        border-radius: 30px;
        width: 100%;
        font-size: 21px;
        line-height: 22px;
        position: relative;
        margin-top: 70px;
       display: flex;
       justify-content: space-between;
        align-items: center;
        padding: 0 30px 0 30px;
        &::after {
            content: "";
            position: absolute;
            background: url(../images/Arrow.png);
            display: block;
            top: 23px;
            right: 32px;
            width: 30px;
            height: 16px;  
        }
        &:hover::after {
            transform: translateX(10px);
            -webkit-transform: translateX(10px);
            -moz-transform: translateX(10px);
            -ms-transform: translateX(10px);
            -o-transform: translateX(10px);
        }
    }
    
   
    
    
   
}

